const Noticia = (props) => {

     const {description, name, url} = props.info;
      
     let desc = description.text;

     if(desc ){
          desc = desc.substr(0, 220);
     }

     return(
          
        <a href={url} target="_blank" className=" list-group-item card text-white border-success mb-3">
             <h4 className="card-title">{name.text}</h4>
             <p className="">{desc}</p>
        </a>
       
     );


}

export default Noticia;